<template>
  <div class="fourPage">

    <p class="title" v-on:click="handlePage()">fourPage</p>
    <p>动画测试</p>

    <button id="idb" class="button" v-on:click="handleButton($event)">点击</button>

    <div class="ball-container">
      <div v-for="(ball, index) in balls" v-bind:key="index">
        <transition @before-enter="beforeEnter" @enter="enter" @after-enter="afterEnter">
          <div v-show="ball.show" class="ball" v-bind:id="ball.tag">
            <div class="inner inner-hook">
            </div>
          </div>
        </transition>
      </div>
    </div>

  </div>
</template>

<script>
  import "./fourPage.scss";
  export default {
    data() {
      // console.log("---------- data -----------");
      let self = this;

      return {
        balls: [{
          show: false,
          tag: 1
        }, {
          show: false,
          tag: 2
        }, {
          show: false,
          tag: 3
        }, {
          show: false,
          tag: 4
        }, {
          show: false,
          tag: 5
        }],
        dropBalls: [],
      }
    },
    props: {},
    created() {
      // console.log("---------- created -----------");
    },
    updated() {
      // console.log("---------- updated -----------");
    },
    components: {
      // console.log("---------- components -----------");
    },

    mounted() {
      // console.log("---------- mounted -----------");
      let self = this;

    },

    computed: {},

    watch: {},
    methods: {
      handleButton(el) {
        console.log("第一步");
        for (let i = 0; i < this.balls.length; i++) {
          let ball = this.balls[i];
          if (!ball.show) {
            ball.show = true;
            ball.el = el.target;
            this.dropBalls.push(ball);
            return;
          }
        }
      },

      beforeEnter(el) {
        console.log("第二步----------" + el.id);
        let count = this.balls.length;
        while (count--) {
          let ball = this.balls[count];
          if (ball.show) {
            let rect = ball.el.getBoundingClientRect();
            let x = rect.left - 32;
            let y = -(window.innerHeight - rect.top - 22);
            el.style.display = '';
            el.style.webkitTransform = `translate3d(0,${y}px,0)`;
            el.style.transform = `translate3d(0,${y}px,0)`;
            let inner = el.getElementsByClassName('inner-hook')[0];
            inner.style.webkitTransform = `translate3d(${x}px,0,0)`;
            inner.style.transform = `translate3d(${x}px,0,0)`;
          }
        }
        console.log("第二步 end");
      },
      enter(el) {
        console.log("第三步---------" + el.id);
        this.$nextTick(() => { //点击一次 无效果 的原因是 nextTick执行太快
          console.log("nextTick-----------" + el.id);
          el.style.webkitTransform = 'translate3d(0,0,0)';
          el.style.transform = 'translate3d(0,0,0)';
          let inner = el.getElementsByClassName('inner-hook')[0];
          inner.style.webkitTransform = 'translate3d(0,0,0)';
          inner.style.transform = 'translate3d(0,0,0)';
        });
      },
      afterEnter(el) {
        console.log("第四步-----------" + el.id);
        let ball = this.dropBalls.shift();
        if (ball) {
          ball.show = false;
          el.style.display = 'none';
        }
      },
      handlePage() {
        this.$router.push('/fivePage');
      },
      handleNoop() {
        //noop
      },
    },

  }
</script>



